<template>
    <div class="firendLink">
        <div class="itemTiele">
            <h3 class="yqlj">
                <b></b> 
            </h3>
            <a href="#" target="_blank" class="moreRR"></a>
        </div>
        <div class="linkContent">
            
            <a 
            v-for="(item, index) in listLink"
            :key="index"
            target="_blank" :href="item.listSrc" :title="item.listTitle">{{item.listText}}</a>
        </div>
    </div>
</template>

<script>
    export default {
      props: {
        listLink: {
          type: Array,
          default: []
        }
      }
    }
</script>

<style>
    
</style>



