<template>
  <header class="header">
    <div class="logo">
      <h1></h1>
      <a href="index.html">
        <img src="../../public/img/logo.png" alt="logo">
      </a>
    </div>
  
    <div class="search_wrapper">
      <div class="search_input">
        <input type="search" placeholder="">
        <div class="search_btn">
          <i class="search_icon"></i>
          <span></span>
        </div>
      </div>
    </div>
    <div class="topNav">
      <ul>
        <li><a href="#" target="_blank"></a></li>
        <li><a href="#" target="_blank"></a></li>
        <li><a href="#" target="_blank"></a></li>
      </ul>
    </div>
    <div class="login_area" v-if="!isUserLoggedIn">
      <a href="#" @click="logClick" class="login_btn"></a>
      <a href="#" @click="logClickReg" class="regin_btn"></a>
    </div>
  
    <div class="login_user" v-if="isUserLoggedIn">
        <el-menu mode="horizontal" class="user-menu">
          <el-dropdown trigger="click">
              <div class="user-img">
                <img src="https://avatars2.githubusercontent.com/u/24250627?s=460&v=4" alt="">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item></el-dropdown-item>
              <a href="" @click="logout">
                <el-dropdown-item>
                  
                </el-dropdown-item>
              </a>
              
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu>
    </div>
  
  </header>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  export default {
    computed: {
      ...mapState([
        'isUserLoggedIn'
      ])
    },
    data() {
      return {}
    },
    methods: {
      logClick: function() {
        this.$emit('clickToolShow', true)
      },
      logClickReg: function() {
        this.$emit('clickToolShowReg', true)
      },
      logout: function() {
        this.$store.dispatch('setToken', null)
        this.$store.dispatch('setUser', null)
      }
    }
  }
</script>

<style lang="less">
.login_user{
  float: left;
  margin: 16px 0 0 80px;
}
.user-img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}

.user-img img{
  width: 100%;
  height: 100%;
  background-size: cover;
}
.user-menu{
  border: 0;
}
</style>



